.about {
    width: 100%;
    height: 90vh;
    display: flex;
    flex-direction: column;
  }
  
  .aboutTop {
    width: 100%;
    height: 55%;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
  }
  
  .aboutBottom {
    display: flex;
    flex-direction: column;
    height: 45%;
    align-items: center;
    margin-top: 40px;
  }
  
  .aboutBottom p {
    width: 85vw;
    margin-top: 0;
    padding: 0;
  }
  
  .about .aboutBottom h1 {
    font-weight: 400;
    font-size: 3em;
    color: brown;
    height: 50px;
    display: flex;
    justify-content: center;
    transform: translateY(-40px);
    text-align: center;
    margin-bottom: 0;
    
  }