.footer {
    width: 100%;
    height: 75px;
    background-color: brown;/*#121619;*/
    display: flex;
    justify-content: flex-end;
    align-items: center;
    flex-direction: column;
    padding-top: 10px;
    padding-bottom: 0;
    box-shadow: 2px 2px 8px rgba(0, 0, 0, 0.8);
    text-align: center;
  }
  
  .socialMedia svg {
    color: whitesmoke;
    font-size: 30px;
    cursor: pointer;
  }
  
  .footer p {
    color: whitesmoke;
    margin: 0 auto;
  }